/* Sign Un page */
.page{
  width: 100vw;
  height: 90vh;
  display: flex;
  align-items: center;
  justify-content: center;

}
.wrapper{
  display: grid;
  width: 70%;
}
.signupContainer{
  grid-template-columns: 45% 55% ;
}

.formside{
  display: flex;
  flex-direction: column;
  padding: 1rem 3rem;
  text-align: center;
  margin-left: 3rem;
}
.formside> h2{
  margin-bottom: .8rem;
}
.formside> p{
  margin-top: 1rem;
}

.signup-Form{
  display: flex;
  flex-direction: column;
  gap:1rem;
}
.signup-Form> input{
  border-radius: 6px;
  padding: .3rem .6rem;
  border: 1px solid grey;
}
 button{
  /* margin-bottom: 1rem; */
  padding: .3rem 1.2rem;
  border: none;
  background-color: #2C2E43;
  color: white;
}
.googlebtn{
  background-color: #185ADB;
  margin-bottom: 1rem;
}
.googlebtn> svg{
  font-size: 2rem;
  margin-right: 1rem;
}

.imgside{
  display: flex;
  align-items: center;
  justify-content: center;

}
.img{
  width: 65%;
}
.signinimg{
  width:60%;
}
.signinContainer{
  grid-template-columns: 45% 45% 10%;
}

.forgetpass{
  text-align: right;
  cursor: pointer;
  
}
.loaderBox{
  width: 100vw;
  height: 80vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.loader{
height: 8rem;
}

@media screen and (max-width:1007px){
  .wrapper{
    width: 100%;
    display: flex;
   align-items: center;
   justify-content: center;
  }
  .imgside{
    width: 50%;
  }
  .formside{
    margin: 0 auto;
  }
}


@media screen and (max-width:640px){
  .wrapper{
    width: 100%;
    display: flex;
   align-items: center;
   justify-content: center;
  }
  .imgside{
    width: 60%;
  }
  .signinContainer{
   flex-direction: column;
  }
  .signupContainer{
 flex-direction: column-reverse;
  }
  .formside{
    margin: 0 auto;
  }
}